#block-views-block-home-page-latest-work {
    margin: 100px 0;

    h2 {
        margin: 0 auto;
        max-width: 1320px;
        border-bottom: 1px solid #000;
        padding-bottom: 10px;
    }

    .latestwork__grid {
        max-width: 1320px;
        margin: auto;
        margin-top: 50px;

        .slick-slide {
            display: flex !important;
            justify-content: center;
        }

        .next-arrow {
            background-color: unset !important;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: unset;
            border-style: unset;
            border-color: unset;
            position: absolute;
            top: -72px;
            right: 44px;

            transform: translateY(-50%);
            cursor: pointer;

            @media (min-width: 992px) {
                margin-left: 1rem;
            }

            &::before {
                content: '<';
                font-family: fontAwesome;
                color: #000;
                font-size: 48px;
                top: -9px;
                position: absolute;
                left: 5px;
                font-weight: 200;
            }
        }

        .prev-arrow {
            background-color: unset !important;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: unset;
            border-style: unset;
            border-color: unset;
            position: absolute;
            top: -72px;
            right: 0;
            transform: translateY(-50%);
            z-index: 100;
            cursor: pointer;

            @media (min-width: 992px) {
                margin-right: 1rem;
            }

            &::before {
                content: '\3e';
                font-family: fontAwesome;
                color: #000;
                font-size: 3rem;
                top: -8px;
                position: absolute;
                left: 11px;
                font-weight: 200;
            }
        }
    }
}